body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-align: center;
}

/* header */

.header {
  background-color: #ce2829;
  padding: 2rem;
  margin: 3rem auto;
}

h1 {
  text-align: center;
  color: white;
  font-weight: bold;
  font-family: "Courier New", Courier, monospace;
}

nav ul {
  list-style-type: none;
  display: flex;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  font-family: "Courier New", Courier, monospace;
  color: white;
  margin-left: 26%;
}

/* container */
p {
  color: #5f5f5f;
}

.total {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 30%;
  margin-right: 30%;
  flex-direction: column;
  margin-bottom: 5rem;
  line-height: 1.5;
}
.container,
.orderPart {
  font-family: "Barlow";
}

.labelCheck {
  color: #292929;
  font-weight: bold;
}

.pizzaInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.price {
  font-size: 20px;
  font-weight: bold;
}
.puan {
  color: #5f5f5f;
  display: flex;
  grid-gap: 6rem;
  gap: 6rem;
}

.sizeDough {
  display: flex;
  grid-gap: 10rem;
  gap: 10rem;
}

.pizzaSize {
  display: flex;
  flex-direction: column;
}

span.star {
  color: #ce2829;
}
.ingredients {
  display: flex;
  flex-direction: column;
}

/*order part */

.orderPart {
  display: flex;
  justify-content: space-between;
}

.sumOrder {
  border: 1px solid;
  padding: 1rem 4rem;
  border-radius: 5%;
}
.boxButton {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}
button {
  background-color: #fdc913;

  border: none;
}
button.order-button {
  background-color: #fdc913;
  border-radius: 10px;
  padding: 1rem;
  font-weight: bold;
}

button.order-button a {
  color: #292929;
}
hr.lastPart {
  width: 100%;
}

.numberButton {
  display: flex;
  flex-direction: row;
  height: 3rem;
}

.countButton {
  width: 3rem;
  border-radius: 4px;
}
#count {
  width: 3rem;
}
#special-text {
  width: 100%;
  height: 2.5rem;
  border-radius: 5%;
  border: 1px solid #5f5f5f;
}
.ingredientSum,
.totalSum {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: bold;
}

.totalSum {
  color: #ce2829;
}

/*contact*/
.contact {
  display: flex;
  flex-direction: column;
}
#name,
#email {
  width: 40%;
}
#address {
  height: 3rem;
}

.home {
  background-color: #ce2829;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

a {
  color: white;
  text-decoration: none;
}

.homeH {
  padding-top: 3rem;
  font-size: 30px;
  padding-bottom: 3rem;
}

.homeText {
  font-family: "Barlow";
  color: white;
  font-size: 60px;
  margin: 1px;
}

.homeButton {
  width: 12rem;
  height: 4rem;
  border-radius: 50px;
  font-weight: bold;
  border: none;
  margin-top: 2rem;
}

.success {
  background-color: #ce2829;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.successH {
  padding-top: 5rem;
  font-size: 30px;
  padding-bottom: 7rem;
}

.successText {
  font-family: "Barlow";
  color: white;
  font-size: 50px;
  margin: 1rem;
}

